___CSS_LOADER_EXPORT___.push([module.id, `.project-container {
    display: flex;
    justify-content: space-between;
    padding: 20px;
    gap: 20px;
}

.project-column {
    flex: 1;
    background: #f8f9fa;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.column-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.column-header h2 {
    margin: 0;
    font-size: 1.5em;
    color: #333;
}

.create-project-btn {
    padding: 10px 15px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 0.9em;
    transition: background-color 0.3s, transform 0.2s;
}

.create-project-btn:hover {
    background-color: #0056b3;
    transform: scale(1.05);
}
